import axios from 'axios';

const apiClient = axios.create({
  baseURL: 'http://localhost:8080/api',
  headers: {
    'Content-Type': 'application/json',
  }
});

export default {
  // Pegar todos os diretórios
  getDirectories() {
    return apiClient.get('/diretorios');
  },

  // Criar um novo diretório
  createDirectory(diretorio) {
    return apiClient.post('/diretorios', diretorio);
  },

  // Atualizar um diretório
  updateDirectory(id, diretorio) {
    return apiClient.put(`/diretorios/${id}`, diretorio);
  },

  // Deletar um diretório
  deleteDirectory(diretorioId) {
    return apiClient.delete(`/diretorios/${diretorioId}`);
  },

  // Adicionar um arquivo a um diretório
  addFile(diretorioId, arquivo) {
    return apiClient.post(`/diretorios/${diretorioId}/arquivos`, arquivo);
  },

  // Adicionar um arquivo a um subdiretório
  addFileToSubdirectory(diretorioId, subId, arquivo) {
    return apiClient.post(`/diretorios/${diretorioId}/subdiretorios/${subId}/arquivos`, arquivo);
  },
  
  // Atualiza o nome do Subdiretório
  updateSubDirectory(diretorioId, subDiretorioId, subDiretorio) {
    return apiClient.put(`/diretorios/${diretorioId}/subdiretorios/${subDiretorioId}`, subDiretorio);
  },
  
  // Editar Arquivos
  updateFile(diretorioId, arquivoId, arquivo) {
    return apiClient.put(`/diretorios/${diretorioId}/arquivos/${arquivoId}`, arquivo);
  },

  // Excluir um arquivo de um diretório
  deleteFile(diretorioId, arquivoId) {
    return apiClient.delete(`/diretorios/${diretorioId}/arquivos/${arquivoId}`);
  },

  // Adicionar um subdiretório
  createSubDirectory(diretorioId, subDiretorio) {
    return apiClient.post(`/diretorios/${diretorioId}/subdiretorios`, subDiretorio);
  },

  // Excluir um subdiretório
  deleteSubDirectory(diretorioId, subDiretorioId) {
    return apiClient.delete(`/diretorios/${diretorioId}/subdiretorios/${subDiretorioId}`);
  }
};
