<template>
  <div id="app">
    <nav class="navbar">
      <h1>Exemplo de Hierarquia de Diretórios</h1>
    </nav>

    <!-- Botão para entrar no modo de gerenciamento -->
    <button @click="toggleManageMode">{{ isManaging ? 'Voltar' : 'Gerenciar Diretórios' }}</button>

    <!-- Se estiver no modo de gerenciamento, mostra os campos de criação -->
    <div v-if="isManaging" class="create-actions">
      <h3>Criar Novo Diretório</h3>
      <input v-model="novoDiretorio" placeholder="Nome do diretório" />
      <button @click="createDirectory">Criar Diretório</button>
    </div>

    <!-- Árvore de Diretórios -->
    <div class="directory-tree">
      <ul>
        <li>
          <span>/</span>
          <ul>
            <li v-for="diretorio in diretorios" :key="diretorio.id">
              <span>{{ diretorio.nome }}</span>

              <!-- Mostrar botões de gerenciamento apenas no modo de gerenciamento -->
              <div v-if="isManaging">
                <button @click="editarDiretorio(diretorio)">Editar</button>
                <button @click="deleteDirectory(diretorio.id)">Excluir Diretório</button>
                <input v-if="diretorioEditado && diretorioEditado.id === diretorio.id" v-model="diretorioEditado.nome" placeholder="Novo nome" />
                <button v-if="diretorioEditado && diretorioEditado.id === diretorio.id" @click="saveEditedDirectory">Salvar Edição</button>

                <!-- Criar Subdiretório -->
                <input v-model="novoSubDiretorio" placeholder="Nome do subdiretório" />
                <button @click="createSubDiretorio(diretorio.id)">Criar Subdiretório</button>
              </div>

              <!-- Subdiretórios e Arquivos (Visível no modo de visualização e gerenciamento) -->
              <ul v-if="diretorio.subDiretorios.length">
                <li v-for="subdir in diretorio.subDiretorios" :key="subdir.id">
                  <span>{{ subdir.nome }}</span>

                  <!-- Gerenciamento de subdiretórios -->
                  <div v-if="isManaging">
                    <button @click="editarSubDiretorio(diretorio.id, subdir)">Editar Subdiretório</button>
                    <input v-if="subDiretorioEditado && subDiretorioEditado.id === subdir.id" v-model="subDiretorioEditado.nome" placeholder="Novo nome" />
                    <button v-if="subDiretorioEditado && subDiretorioEditado.id === subdir.id" @click="saveEditedSubDiretorio">Salvar Edição</button>

                    <button @click="deleteSubDiretorio(diretorio.id, subdir.id)">Excluir Subdiretório</button>
                  </div>

                  <!-- Criar Arquivo no Subdiretório -->
                  <ul v-if="subdir.arquivos.length || isManaging">
                    <li v-for="arquivo in subdir.arquivos" :key="arquivo.id">
                      {{ arquivo.nome }}
                      <button v-if="isManaging" @click="editarArquivo(subdir.id, arquivo)">Editar Arquivo</button>
                      <input v-if="arquivoEditado && arquivoEditado.id === arquivo.id" v-model="arquivoEditado.nome" placeholder="Novo nome" />
                      <button v-if="arquivoEditado && arquivoEditado.id === arquivo.id" @click="saveEditedArquivo">Salvar Edição</button>

                      <button v-if="isManaging" @click="deleteFile(subdir.id, arquivo.id)">Excluir Arquivo</button>
                    </li>

                    <li v-if="isManaging">
                      <input v-model="novoArquivo" placeholder="Nome do arquivo" />
                      <button @click="createFileInSubdirectory(diretorio.id, subdir.id)">Criar Arquivo</button>
                    </li>
                  </ul>
                </li>
              </ul>

              <!-- Lista de arquivos no diretório principal -->
              <ul v-if="diretorio.arquivos.length">
                <li v-for="arquivo in diretorio.arquivos" :key="arquivo.id">
                  {{ arquivo.nome }}
                  <button v-if="isManaging" @click="editarArquivo(diretorio.id, arquivo)">Editar Arquivo</button>
                  <input v-if="arquivoEditado && arquivoEditado.id === arquivo.id" v-model="arquivoEditado.nome" placeholder="Novo nome" />
                  <button v-if="arquivoEditado && arquivoEditado.id === arquivo.id" @click="saveEditedArquivo">Salvar Edição</button>

                  <button v-if="isManaging" @click="deleteFile(diretorio.id, arquivo.id)">Excluir Arquivo</button>
                </li>
              </ul>
            </li>
          </ul>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import apiService from './services/apiService';

export default {
  data() {
    return {
      diretorios: [],
      isManaging: false, // Modo de gerenciamento
      novoDiretorio: '', // Novo diretório
      novoSubDiretorio: '', // Novo subdiretório
      novoArquivo: '', // Novo arquivo
      diretorioEditado: null, // Para edição de diretórios
      subDiretorioEditado: null, // Para edição de subdiretórios
      arquivoEditado: null, // Para edição de arquivos
      diretorioPaiEditadoId: null, // ID do diretório pai para subdiretório editado
      diretorioArquivoEditadoId: null // ID do diretório pai para arquivo editado
    };
  },
  methods: {
    fetchDirectories() {
      apiService.getDirectories()
        .then(response => {
          this.diretorios = response.data.map(diretorio => ({
            ...diretorio,
            arquivos: diretorio.arquivos || [],
            subDiretorios: diretorio.subDiretorios || []
          }));
        })
        .catch(error => {
          console.error('Erro ao buscar diretórios:', error);
        });
    },
    createDirectory() {
      if (!this.novoDiretorio) return;
      apiService.createDirectory({ nome: this.novoDiretorio })
        .then(() => {
          this.novoDiretorio = '';
          this.fetchDirectories();
        })
        .catch(error => {
          console.error('Erro ao criar diretório:', error);
        });
    },
    createSubDiretorio(parentId) {
      if (!this.novoSubDiretorio) return;
      apiService.createSubDirectory(parentId, { nome: this.novoSubDiretorio })
        .then(() => {
          this.novoSubDiretorio = '';
          this.fetchDirectories();
        })
        .catch(error => {
          console.error('Erro ao criar subdiretório:', error);
        });
    },
    createFileInSubdirectory(diretorioId, subId) {
      if (!this.novoArquivo) return;

      apiService.addFileToSubdirectory(diretorioId, subId, { nome: this.novoArquivo })
        .then(() => {
          this.novoArquivo = '';
          this.fetchDirectories();
        })
        .catch(error => {
          console.error('Erro ao criar arquivo no subdiretório:', error);
        });
    },
    editarSubDiretorio(diretorioId, subdir) {
      this.subDiretorioEditado = { ...subdir };
      this.diretorioPaiEditadoId = diretorioId;
    },
    editarDiretorio(diretorio) {
      this.diretorioEditado = { ...diretorio };
    },
    editarArquivo(diretorioId, arquivo) {
      this.arquivoEditado = { ...arquivo };
      this.diretorioArquivoEditadoId = diretorioId;
    },
    saveEditedArquivo() {
      apiService.updateFile(this.diretorioArquivoEditadoId, this.arquivoEditado.id, { nome: this.arquivoEditado.nome })
        .then(() => {
          this.arquivoEditado = null;
          this.diretorioArquivoEditadoId = null;
          this.fetchDirectories();
        })
        .catch(error => {
          console.error('Erro ao editar arquivo:', error);
        });
    },
    saveEditedDirectory() {
      apiService.updateDirectory(this.diretorioEditado.id, { nome: this.diretorioEditado.nome })
        .then(() => {
          this.diretorioEditado = null;
          this.fetchDirectories();
        })
        .catch(error => {
          console.error('Erro ao editar diretório:', error);
        });
    },
    saveEditedSubDiretorio() {
      apiService.updateSubDirectory(this.diretorioPaiEditadoId, this.subDiretorioEditado.id, { nome: this.subDiretorioEditado.nome })
        .then(() => {
          this.subDiretorioEditado = null;
          this.diretorioPaiEditadoId = null;
          this.fetchDirectories();
        })
        .catch(error => {
          console.error('Erro ao editar subdiretório:', error);
        });
    },
    deleteFile(diretorioId, arquivoId) {
      apiService.deleteFile(diretorioId, arquivoId)
        .then(() => {
          this.fetchDirectories();
        })
        .catch(error => {
          console.error('Erro ao excluir arquivo:', error);
        });
    },
    deleteSubDiretorio(diretorioId, subId) {
      apiService.deleteSubDirectory(diretorioId, subId)
        .then(() => {
          this.fetchDirectories();
        })
        .catch(error => {
          console.error('Erro ao excluir subdiretório:', error);
        });
    },
    deleteDirectory(diretorioId) {
      apiService.deleteDirectory(diretorioId)
        .then(() => {
          this.fetchDirectories();  // Atualiza a lista de diretórios após a exclusão
          alert("Diretório excluído com sucesso!");
        })
        .catch(error => {
          if (error.response && error.response.status === 409) {
            alert("Não é possível excluir o diretório porque ele contém subdiretórios ou arquivos.");
          } else if (error.response && error.response.status === 404) {
            alert("Diretório não encontrado.");
          } else {
            alert("Erro ao excluir o diretório. Tente novamente.");
          }
        });
    },
    toggleManageMode() {
      this.isManaging = !this.isManaging; // Alterna entre modo de visualização e gerenciamento
    }
  },
  mounted() {
    this.fetchDirectories();
  }
};
</script>

