.directory-tree ul {
  list-style-type: none;
  padding-left: 20px;
  font-family: monospace;
  font-size: 16px;
  color: #333;
}

.directory-tree li {
  margin: 5px 0;
}

.directory-tree span {
  font-weight: bold;
  color: #007bff;
}

.directory-tree li::before {
  content: "├──";
  padding-right: 10px;
}

.directory-tree li:last-child::before {
  content: "└──";
}

.directory-tree ul ul li::before {
  content: "    ├──";
}

.directory-tree ul ul li:last-child::before {
  content: "    └──";
}

